import { Component, h, State, Prop, Event } from '@stencil/core';
import { EnumViews } from '../../models/EnumViews';
import { EventEmitter } from 'events';
import { IListItem } from '../../types/ListItem';

@Component({
    tag: 'layout-menu',
    shadow: true,
    styleUrl: 'layout-menu.scss',
})
export class LayoutMenu {
    @Prop() public activeView: EnumViews;
    @Prop() public isSending: boolean;

    @State() private menuItems: IListItem[] = [
        {
            text: 'Help',
            icon: 'ask_question',
            value: EnumViews.help,
        },
        {
            text: 'Settings',
            icon: 'settings',
            value: EnumViews.settings,
        },
        {
            text: 'Logout',
            icon: 'exit',
            value: EnumViews.logout,
        },
    ];
    @State() private isOpen: boolean = false;

    @Event() changeView: EventEmitter;

    constructor() {
        this.toggleMenu = this.toggleMenu.bind(this);
        this.handleBack = this.handleBack.bind(this);
        this.showBackButton = this.showBackButton.bind(this);
        this.previousViewOnClose = this.previousViewOnClose.bind(this);
        this.onNavigate = this.onNavigate.bind(this);
        this.onCancelMenu = this.onCancelMenu.bind(this);
    }

    public render() {
        if (this.isSending) {
            return [];
        }

        if (this.showBackButton()) {
            return (
                <limel-button
                    class="ga-menu"
                    onClick={this.handleBack}
                    label="Back"
                />
            );
        }

        return (
            <limel-menu
                class="ga-menu"
                items={this.menuItems}
                onCancel={this.onCancelMenu}
                onSelect={this.onNavigate}
                open={this.isOpen}
            >
                <div slot="trigger">
                    <limel-icon-button icon="menu" onClick={this.toggleMenu} />
                </div>
            </limel-menu>
        );
    }

    private toggleMenu() {
        this.isOpen = !this.isOpen;
    }

    private handleBack() {
        this.changeView.emit(this.previousViewOnClose());
    }

    private onNavigate(event) {
        this.changeView.emit(event.detail.value);
        this.isOpen = false;
    }

    private onCancelMenu() {
        this.isOpen = false;
    }

    private previousViewOnClose(): EnumViews {
        switch (this.activeView) {
            case EnumViews.videoLibrary:
                return EnumViews.sendDocument;
            case EnumViews.invite:
                return EnumViews.home;
            case EnumViews.help:
                return EnumViews.home;
            case EnumViews.settings:
                return EnumViews.home;
            case EnumViews.documentDetail:
                return EnumViews.home;
            case EnumViews.documentValidation:
                return EnumViews.sendDocument;
            default:
                return this.activeView;
        }
    }

    private showBackButton(): boolean {
        switch (this.activeView) {
            case EnumViews.videoLibrary:
                return true;
            case EnumViews.invite:
                return true;
            case EnumViews.help:
                return true;
            case EnumViews.settings:
                return true;
            case EnumViews.documentDetail:
                return true;
            case EnumViews.documentValidation:
                return false;
            default:
                return false;
        }
    }
}
